import React, { useEffect, useState } from 'react'
import './style.css'
const Third = () => {
  const [Items, setItems] = useState([])
  const setItemsArray = () => {
    setItems([...Items, {
      aid: Items.length,
      name: 'Maria',
      surname: 'Fenyane'
    },
    {
      aid: Items.length,
      name: 'Monicca',
      surname: 'Fenyane'
    },
    {
      aid: Items.length,
      name: 'Nhlanhla',
      surname: 'Mateng'
    },
    {
      aid: Items.length,
      name: 'Tinti',
      surname: 'Molepo'
    }

    ])
  }

  const [counter, setCounter] = useState(10)
  useEffect(() => {
    const timer =
   counter > 0 && setInterval(() => setCounter(counter - 1), 1000)
    return () => clearInterval(timer)
  }, [counter])

  return (
    <div className='container'>
      <div className='wrapper'>
        <h2>Press Button when counter reaches 0</h2>
      </div>
      <div className='Timer'> :{counter + ' ' + 'secs'}</div>
      {/* <button className='btnCounter' disabled>Press Below in: {counter}</button> */}
      <div> <button className='button' onClick={setItemsArray}>View List Of Names</button>
      </div>
      <ul>
        {Items.map(element => <li key={element.aid}> {element.name + ' ' + element.surname}</li>)}

      </ul>
    </div>
  )
}

export default Third
